body {
    background-color: gray;
    color: white;
    font-family: 'Josefin Sans', sans-serif;
}

.products {
    display: flex;
    flex-wrap: wrap;
}

.product-card {
    padding: 2%;
    flex-grow: 1;
    flex-basis: 16%;
    font-size: 17px;
}

.cont {
    display: flex;
    justify-content: center;
    
}

.sales {
    color: red;
    font-style: bold;
}

.navidation {
    display: flex;
    justify-content: space-between;
    padding: 1% 3%;
}

.link {
    padding: 1% 3% ;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    font-size: 18px;
}

.sales {
    color: red;
    font-style: bold;
}

.back {
    padding: 10px;
    margin:  0 20px;
}

nav {
    display: flex;
    justify-content: center;
    background-color: black;
    padding: 15px;
    font-family: 'Josefin Sans', sans-serif;
}

.cahnge {
    background-color: black;
    color: white;
    padding: 10px;
    font-family: 'Josefin Sans', sans-serif;
    border: none;
}

button, input {
    margin: 10px;
    padding: 5px 30px;
    font-family: 'Josefin Sans', sans-serif;
    border-radius: 20px/20px;
    font-size: 16px;
    border: none;
}

.price, .name {
    font-size: 21px;
    font-weight: bold;
    padding: 2% 0%;
}

.price {
    color: black;
}

.showMore {
    margin: 0px;
    width: auto;
    font-size: small;
}